<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 角色列表 -->
    <el-card>
      <el-button type="primary" icon="el-icon-user" @click="addRoleVisible = true">添加角色</el-button>
      <el-table :data="rolesList" border>
        <!-- 下拉菜单(角色权限显示) -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row v-for="(item1,i1) in scope.row.children" :key="item1.id" :class='["borderBottom","vcenter",i1===0?"borderTop":""]'>
              <!-- 渲染一级权限 -->
              <el-col :span="6">
                <el-tag closable @close="deleteRightsTag(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="18">
                <el-row v-for="(item2, i2) in item1.children" :key="item2.id" :class="['vcenter',i2===0?'':'borderTop']">
                  <el-col :span="6">
                    <el-tag closable type="success" @close="deleteRightsTag(scope.row,item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag class="tag" closable type="warning" v-for="(item3) in item2.children" :key="item3.id" @close="deleteRightsTag(scope.row,item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引 -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="editRole(scope.row.id)">编辑</el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteRole(scope.row.id)">删除</el-button>
            <el-button type="warning" size="mini" icon="el-icon-setting" @click="setRoleRights(scope.row)">分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 创建角色 -->
    <el-dialog title="添加角色" :visible.sync="addRoleVisible" @close="addRoleClose">
      <el-form :model="addRoleForm" :rules="addRoleRules" ref="addRoleRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoleVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRoleSubmit(addRoleForm.roleName,addRoleForm.roleDesc)">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑角色 -->
    <el-dialog title="编辑角色" :visible.sync="editRoleVisible" @close="editRoleClose">
      <el-form :model="editRoleForm" :rules="editRoleRules" ref="editRoleRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述">
          <el-input v-model="editRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRoleVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRoleSubmit(editRoleForm.roleId,editRoleForm.roleName,editRoleForm.roleDesc)">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 设置角色权限 -->
    <el-dialog title="提示" :visible.sync="setRightsVisible" @close="setRoleClose">
      <el-tree :data="rightsList" :props="treeProps" node-key="id" default-expand-all show-checkbox :default-checked-keys="defaultKeys" ref="rightsTree"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightsVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotPower">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 角色列表
      rolesList: [],

      // 角色权限列表
      rightsList: [],
      // tree组件中需要填入的数据
      treeProps: {
        children: 'children', // 指定子树为节点对象的某个属性值
        label: 'authName', // 指定节点标签名称为节点对象的某个属性值
      },
      // 默认勾选的节点的 key 的数组
      defaultKeys: [],
      // 需要添加权限的角色id
      roleId: '',

      // 添加角色的dialog显示与隐藏
      addRoleVisible: false,
      // 编辑角色的dialog显示与隐藏
      editRoleVisible: false,
      // 设置角色权限的dialog的显示与隐藏
      setRightsVisible: false,
      // 添加角色的表单信息
      addRoleForm: {
        roleId: '',
        roleName: '',
        roleDesc: '',
      },
      // 编辑角色的表单信息
      editRoleForm: {
        roleId: '',
        roleName: '',
        roleDesc: '',
      },
      // 添加角色的表单验证规则
      addRoleRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' },
        ],
      },
      // 编辑角色的表单验证规则
      editRoleRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' },
        ],
      },
    }
  },

  methods: {
    // 初始化数据
    async initRolesList() {
      let { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200)
        return this.$message.error('获取角色列表失败!')
      this.rolesList = res.data
    },

    // 添加角色表单点击提交
    addRoleSubmit(roleName, roleDesc) {
      let obj = {
        roleName,
        roleDesc,
      }
      this.$refs.addRoleRef.validate(async (valid) => {
        if (!valid) return false
        let { data: res } = await this.$http.post('roles', obj)
        if (res.meta.status !== 201) return this.$message.error('创建角色失败!')
        this.$message.success('创建角色成功!')
        this.initRolesList()
        this.addRoleVisible = false
      })
    },
    // 关闭添加角色表单
    addRoleClose() {
      this.$refs.addRoleRef.resetFields()
      this.addRoleForm.roleDesc = ''
    },

    // 点击编辑角色按钮
    async editRole(id) {
      this.editRoleVisible = true
      let { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200)
        return this.$message.error('获取角色信息失败!')
      this.editRoleForm = res.data
    },
    // 关闭编辑角色表单
    editRoleClose() {
      this.$refs.editRoleRef.resetFields()
    },
    // 提交编辑角色表单
    editRoleSubmit(id, roleName, roleDesc) {
      let obj = {
        roleName,
        roleDesc,
      }
      this.$refs.editRoleRef.validate(async (valid) => {
        if (!valid) return false
        let { data: res } = await this.$http.put('roles/' + id, obj)
        if (res.meta.status !== 200) return false
        this.$message.success('修改角色信息成功!')
        this.editRoleVisible = false
        this.initRolesList()
      })
    },

    // 删除角色
    deleteRole(id) {
      this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(async () => {
          let { data: res } = await this.$http.delete('roles/' + id)
          if (res.meta.status !== 200)
            return this.$message.error('删除角色失败!')
          this.$message.success('删除角色成功!')
          this.initRolesList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },

    // 删除角色权限(tag标签)
    async deleteRightsTag(role, rightId) {
      let result = await this.$confirm(
        '此操作将永久删除该权限, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      if (result !== 'confirm') return this.$message.info('取消了删除')
      let { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) return this.$message.error('删除权限失败!')
      this.$message.success('删除权限成功!')
      // this.initRolesList() // 如果使用这个初始化数据,会刷新页面导致下拉界面被收缩
      role.children = res.data // 使用这个就不会了
    },

    // 关闭分配角色权限的dialog
    setRoleClose() {
      // 因为在每次点击分配角色按钮时都会往数组中添加数据,
      // 需要清空,否则多次点击按钮会进行数组内容叠加
      this.defaultKeys = []
    },
    // 设置角色权限按钮
    async setRoleRights(roles) {
      this.roleId = roles.id
      let { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200)
        return this.$message.error('获取角色权限列表失败!')
      this.rightsList = res.data

      // 将权限添加到递归函数中,添加到默认勾选的节点的 key 的数组中
      this.getLeafKeys(roles, this.defaultKeys)

      this.setRightsVisible = true
    },
    // 分配角色权限
    async allotPower() {
      // 将半选中和全选择的节点id数组进行合并(方法是tree组件提供的)
      let arr = [
        ...this.$refs.rightsTree.getCheckedKeys(),
        ...this.$refs.rightsTree.getHalfCheckedKeys(),
      ]
      let str = arr.join(',') // api接口需要的请求体数据
      // id无法在当前事件中获取，需要在点击分配权限按钮时，将传入的数据中的id添加到data属性roleid中，再在此处调用
      let { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, {
        rids: str,
      })
      if (res.meta.status !== 200) return this.$message.error('权限修改失败!')
      this.$message.success('权限修改成功!')
      console.log(res)
      this.initRolesList()
      this.setRightsVisible = false
    },
    // 使用递归来将三级权限添加到默认勾选的节点的 key 的数组中
    getLeafKeys(node, arr) {
      // 如果已经是三级节点了(没有children属性),就添加到数组
      if (!node.children) {
        return arr.push(node.id)
      }
      // 节点下还有children属性,循环每一个children下的子节点
      node.children.forEach((item) => {
        this.getLeafKeys(item, arr)
      })
    },
  },

  created() {
    this.initRolesList()
  },
}
</script>

<style lang="less" scoped>
.el-row {
  margin: 0 50px;
  padding: 10px;
}
.tag {
  margin: 5px;
}
.borderTop {
  border-top: 1px solid #ededed;
}
.borderBottom {
  border-bottom: 1px solid #ededed;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>